import Fetch from 'controllers/fetch';

export const requestUs = (text:string, file:FormData) => {
  const sendData = {
    text,
    file
  }

  return Fetch("/api/v1/user/question", "POST", sendData)
      .then(res => {
        return res.success;
      })
      .catch(err => {
        return err;
      })
}